




















































































































































import {
  Component,
  Mixins
} from 'vue-property-decorator'
import IntegralMix from "@/mixins/integral";
@Component({})
export default class Integral extends Mixins(IntegralMix) {

  name = "integral";

  get isCurrent() {
    return this.$STORE.setStore.currentTab === this.name;
  }

  // 组件周期函数--监听组件挂载完毕
  mounted() {
    uni.setNavigationBarTitle({
      title: "积分商城",
    });
    this.getShopType();
    this.getUser();
    this.getCoupon();
  }
}
